    algorithmInformation: {
      lingo: ' <h4>Algorithm</h4> <p>The presented news feed was generated by Lingo, an algorithm from the open-source framework <a href="https://project.carrot2.org/">Carrot2</a>.</p> <h4>How does it work?</h4> <p>Lingo\'s operation can be described, in simple terms, as a 4 step sequence.</p> <h5>Pre-processing</h5> <p>First, the news articles are pre-processed. In this phase are applied to the text techniques such as <i>tokenization</i> - transforming the text into a list of individual words -, <i>stemming</i> - reducing inflected words to their word stem -, and the removal of <i>stop words</i> - a language\'s most common words. The goal is to reduce the list of units of text to analyse without impacting their relevance.</p> <h5>Phrase extraction</h5> <p>After pre-processing, the most frequent phrases are extracted from the text. These phrases are used to determine the <i>labels</i> (the "Topics" that are shown on the app\'s sidebar) for each group (<i>cluster</i>) of news articles.</p> <h5>Cluster content discovery</h5> <p>Based on the proximity between the identified <i>labels</i> and the articles\' text, the algorithm determines which articles are to be associated with each <i>label</i>. Articles to which is not possible associate a <i>label</i> are not included in the <i>clusters</i>. <h5>Final cluster formation</h5> <p>Finally, the algorithm forms the <i>clusters</i>, evaluates their score, orders them, and displays them.</p> <h4>References</h4> <p>The following references from Lingo\'s authors allow a deeper inspection of the algorithm\'s operation:</p> <ul> <li> <a href="http://www.cs.put.poznan.pl/dweiss/site/publications/slides/iipwm2004-dweiss-lingo.pdf">Lingo: Search Results Clustering AlgorithmBased on Singular Value Decomposition (Presentation)</a> </li> <li> <a href="https://pdfs.semanticscholar.org/34f5/4619167ea03f99f6e5706d31df16a80b15a2.pdf">Lingo: Search results clustering algorithm based on singular value decomposition (Paper)</a> </li> </ul>',
      lsh: ' <h4>Algorithm</h4> <p>The presented news feed was generated by an algorithm based on Locality-sensitive Hashing (LSH).</p> <h4>How does it work?</h4> <p>The algorithm\'s operation can be described, in simple terms, as a 3 step sequence.</p> <h5>Pre-processing</h5> <p>First, the news articles are pre-processed. In this phase are applied to the text techniques such as the removal of whitespace and <i>stop words</i> - a language\'s most common words. The text is then converted to tokens comprised of contiguous words - shingles. For instance, the text "This is a news article title", would be converted, using 3 word-shingles, to the following shingles: <ul><li>"This is a"</li><li>"is a news"</li><li>"a news article"</li><li>"news article title"</li></ul></p> <h5>Minhashing</h5> <p>Imagine that the articles we want to check for similarity are 10,000 words long each. After converting the text to 3-word shingles, we would have 9,998 shingles (total word length - shingle size + 1) per article. Since each pair would have to be compared, it\'s easy to imagine that this approach wouldn\'t scale if we wanted to compare thousands of documents.</p> <p>One way to improve the efficiency would be comparing just a subset of randomly selected shingles. The minHash technique allows us to do that, without sacrificing too much accuracy. Each shingle is passed through several different hash functions, and the lowest value stored. Due to the way hashes works, selecting the lowest value is equivalent to a random selection. Additionally, the hashed values are stored as integer numbers of fixed length, which allow faster comparisons than strings of variable length (like the original shingles).</p> <p>So, by using minhashing with, for instance, 200 hash functions, documents with thousands of shingles can be represented by a signature with 200 randomly selected components (each component being the minimum hashed value of a shingle).</p> <h5>Locality-sensitive Hashing</h5> <p>Even though minHash reduces the computational workload needed to compare documents, each document would still need to be compared to every other. Locality-sensitive Hashing (LSH) allows further optimization. LSH finds candidate documents for comparison, i.e., documents for which a fraction of the signature\'s components are similar. Documents which do not have components with common with any other are not processed.</p> <p>This means that, rather than comparing every document with each other, the comparison can be restricted to the candidates. In our implementation, the candidates are determined via LSH, but no comparison is made, since the results have shown to be adequate. For each document, a (configurable) number of signature components is passed through a hash function and put on a \'bucket\' identified by the hashed value. Since the hashed value is the \'bucket\' identifier, every other document whose signature components hash to the same value will (probably) fall in the same \'bucket\'. \'Buckets\' that contain more than one document are then merged with the ones containing at least one document in common and the clusters created and returned.</p>'
    }
