      lingo: ' <h4>Algoritmo</h4> <p>A lista de notcias apresentada foi gerada pelo Lingo, um algoritmo que faz parte da framework open-source <a href="https://project.carrot2.org/">Carrot2</a>.</p> <h4>Como funciona?</h4> <p>O funcionamento do algoritmo Lingo pode ser descrito, de forma simplificada, como uma sequncia de 4 passos.</p> <h5>Pr-processamento</h5> <p>Em primeiro lugar, as notcias so pr-processadas. Nesta fase so aplicadas ao texto tcnicas como a <i>tokenizao</i> - transformar o texto numa lista de palavras individuais -, a <i>stemizao</i> - reduzir palavras flexionadas ao seu tronco -, e a remoo de <i>stop words</i> - as palavras mais comuns de uma lngua. O objectivo  reduzir a lista de unidades de texto a analisar sem afectar a relevncia.</p> <h5>Extrao de frases</h5> <p>Aps o pr-processamento, so extradas as frases mais frequentes do texto. Estas frases so usadas para determinar as <i>labels</i> (os "Tpicos" que aparecem na barra lateral da aplicao) dos grupos de notcias (<i>clusters</i>).</p> <h5>Descoberta do contedo dos <i>clusters</i></h5> <p>Com base na proximidade entre as <i>labels</i> identificadas e o texto dos documentos, o algoritmo determina quais os documentos a associar a cada <i>label</i>. Os documentos aos quais no  possvel atribuir uma <i>label</i> no so includos nos <i>clusters</i>. <h5>Formao final dos <i>clusters</i></h5> <p>Por fim, o algoritmo forma os <i>clusters</i>, avalia a pontuao de cada um, ordena-os, e exibe-os.</p> <h4>Referncias</h4> <p>As seguintes referncias permitem inspecionar com maior detalhe o funcionamento do algoritmo:</p> <ul> <li> <a href="http://www.cs.put.poznan.pl/dweiss/site/publications/slides/iipwm2004-dweiss-lingo.pdf">Lingo: Search Results Clustering AlgorithmBased on Singular Value Decomposition (Presentation)</a> </li> <li> <a href="https://pdfs.semanticscholar.org/34f5/4619167ea03f99f6e5706d31df16a80b15a2.pdf">Lingo: Search results clustering algorithm based on singular value decomposition (Paper)</a> </li> </ul>'
